import { useState } from "react"
import { isFileStatusCompleted } from "@rp/common/utils"
import { FileRowType } from "@rp/common/utils"
import { ActionsMenu } from "@rp/react/web/common/base-components"
import { Actions } from "@rp/react/web/common/types"
import { Box, CircularProgress, IconButton } from "@rp/react/web/mui/core"
import { Delete } from "@rp/react/web/mui/icons"

type Props = {
  item: FileRowType
  actions: Actions<FileRowType>
  onRemove: (item: FileRowType) => void
}

const FilesListMobilesActions = ({ item, actions, onRemove }: Props) => {
  const [open, setOpen] = useState(false)
  if (item.status === "failed") {
    return (
      <IconButton onClick={() => onRemove(item)} sx={{ pr: 0.5 }}>
        <Delete />
      </IconButton>
    )
  }
  if (!isFileStatusCompleted(item.status))
    return (
      <Box
        sx={{
          width: 37,
          textAlign: "center",
          mt: 1,
        }}
      >
        <CircularProgress size={18} />
      </Box>
    )
  return (
    <Box sx={{ display: "flex", justifyContent: "right", alignItems: "center" }}>
      <ActionsMenu open={open} setOpen={setOpen} actions={actions} item={item} />
    </Box>
  )
}

export { FilesListMobilesActions }
